const isASignificantChange = (percent_change: number) => {
  return percent_change > 1 || percent_change < -1;
};

export const setNotification = (coins: any[]) => {
  const coinsToBeNotified = coins.filter((coin: any) =>
    isASignificantChange(coin.quote.USD.percent_change_1h),
  );

  chrome.notifications.create('cryptobase_notification', {
    type: 'basic',
    iconUrl: 'images/1.png',
    title: 'Price change Notification!',
    message: coinsToBeNotified[1].name,
    priority: 2,
  });
};
